import React, {useEffect, useState} from "react";
import {useLocation, useNavigate} from "react-router-dom";
import useWorkDefaultData from "../utils/WorkDataDefault";
import {useAuth} from "../../config/AuthContext";
import createAxiosInstance from "../../config/api"; //     

function WorkScheduleDashboard (){
    const { username } = useAuth();
    const [item, setItem] = useState({});
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState("");
    const [isEditing, setIsEditing] = useState(false);
    const [editedItem, setEditedItem] = useState({});
    const navigate = useNavigate();
    const location = useLocation();
    const workDefaultData = useWorkDefaultData();
    console.log("workDefaultData", workDefaultData);
    useEffect(() => {
        const schedule = location.state;
        if (schedule) {
            setIsEditing(schedule.stateInfo);
        }
    }, [location.state]);


    useEffect(() => {
        const fetchWorkDefaultData = async () => {
            setLoading(true);
            setError("");
            try {
                if(workDefaultData.employeeName === "") {
                    //  employeeName     ( )
                    const defaultData = {
                        employeeId: username, //  
                        employeeName: workDefaultData.employeeName, //    
                        workLocation: "",
                        checkInTime: "",
                        checkOutTime: "",
                        breakTimeIn: "",
                        breakTimeOut: "",
                        basicWorkTime: "",
                    };
                    setItem(defaultData);
                    setEditedItem(defaultData);
                }  else {
                    setItem(workDefaultData);
                    setEditedItem(workDefaultData);
                }
            } catch (error) {
                setError("    .");
                console.error(error);
            } finally {
                setLoading(false);
            }
        };
        fetchWorkDefaultData();
    }, [workDefaultData, username]);

    function handleClick() {
        navigate("/workSchedule/list");
    }

    if (loading) return <p>Loading...</p>;
    if (error) return <p>{error}</p>;

    const handleChange = (e) => {
        const { name, value } = e.target;
        setEditedItem((prevItem) => ({
            ...prevItem,
            [name]: value,
        }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault(); //   
        const confirmSave = window.confirm("  ?");
        if (!confirmSave) {
            return;
        } else {
            setLoading(true);
            setError("");
            try {
                const axiosInstance = createAxiosInstance(); //  
                await axiosInstance.put("/workSchedule/default/update", editedItem);
                // setItem(editedItem);
                window.alert("  .");
                navigate("/workSchedule/list");
            } catch (error) {
                setError("  .");
            } finally {
                setLoading(false);
            }
        }
        };
    if (loading) return <div> ...</div>;
    if (error) return <div>{error}</div>;
    return (
            <div className="detail-container mt-4">
                    <div className="card">
                        <form onSubmit={handleSubmit}>
                            <div className="card-header">
                                <h3> {editedItem.employeeName} </h3>
                            </div>
                            <div className="card-body">
                                <div>
                                    <div className="form-group">
                                        <label className="label">ID</label>
                                        <div>
                                            <input
                                                className="input"
                                                type="text"
                                                name="id"
                                                value={editedItem.employeeId}
                                                onChange={handleChange}
                                                disabled
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label"></label>
                                        <div>
                                            <input
                                                className="input"
                                                type="text"
                                                name="name"
                                                value={editedItem.employeeName}
                                                onChange={handleChange}
                                            disabled
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label"></label>
                                        <div>
                                            <input
                                                className="input"
                                                type="text"
                                                name="workLocation"
                                                value={editedItem.workLocation}
                                                onChange={handleChange}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label"> </label>
                                        <div>
                                            <input
                                                type="number"
                                                className="input"
                                                name="basicWorkTime"
                                                value={editedItem.basicWorkTime}
                                                onChange={handleChange}

                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">  </label>
                                        <div>
                                            <input
                                                type="time"
                                                className="input"
                                                name="checkInTime"
                                                value={editedItem.checkInTime}
                                                onChange={handleChange}
                                            />
                                        </div>
                                    </div>
                                    <div className="form-group">
                                        <label className="label">  </label>
                                        <input
                                            type="time"
                                            className="input"
                                            name="checkOutTime"
                                            value={editedItem.checkOutTime}
                                            onChange={handleChange}

                                        />
                                    </div>
                                    <div className="form-group">
                                        <label className="label"> </label>
                                        <div className="d-flex">
                                            <input
                                                name="breakTimeIn"
                                                type="time"
                                                className="input me-2"
                                                value={editedItem.breakTimeIn}
                                                onChange={handleChange}
                                            />
                                            <div className="text-gray-500 me-2 fs-5	">~</div>
                                            <input
                                                name="breakTimeOut"
                                                type="time"
                                                className="input"
                                                value={editedItem.breakTimeOut}
                                                onChange={handleChange}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="card-footer">

                                    {isEditing ?
                                        <button type="submit" className="btn btn-secondary" ></button>
                                        : <button type="submit" className="btn btn-secondary" >
                                            </button>
                                        }

                                {isEditing ? (
                                    <button type="button" className="btn btn-danger" onClick={handleClick}>
                                        
                                    </button>
                                ) : (
                                    <>
                                    </>
                                )
                                }
                            </div>
                        </form>
                    </div>
            </div>
    );
}
export default WorkScheduleDashboard;