
import React, {useEffect, useState} from 'react';
import {useNavigate} from "react-router-dom";
import 'bootstrap-icons/font/bootstrap-icons.css';
import "../../config/index.css";
import holidayListData from "../../utils/holidayListData";
import useWorkData from "../utils/WorkData";
import useWorkDefaultData from "../utils/WorkDataDefault";
const WorkScheduleList = () =>  {

        const today = new Date();
        const [year, setYear] = useState(today.getFullYear());
        const [month, setMonth] = useState(today.getMonth() + 1);
        const [schedule, setSchedule] = useState([]);
        const navigate = useNavigate();
        const [loading, setLoading] = useState(false);
        const [error, setError] = useState("");
        const workDataList = useWorkData(year, month); //   
    const workDefaultData = useWorkDefaultData();
        console.log(workDefaultData);
    // const dummyData = {
    //     "2025-01-01": { attendanceType: "", workType: "", checkInTime: "", checkOutTime: "", memo: "" },
    //     "2025-01-02": { attendanceType: "", workType: "", checkInTime: "09:00", checkOutTime: "18:00", memo: " " },
    //     "2025-01-03": { attendanceType: "", workType: "", checkInTime: "", checkOutTime: "", memo: " " },
    //     "2025-01-06": { attendanceType: "", workType: "", checkInTime: "09:00", checkOutTime: "18:00", memo: "" },
    //     "2025-02-03": { attendanceType: "", workType: "", checkInTime: "09:00", checkOutTime: "18:00", memo: "" },
    // };

    useEffect(() => {
        const fetchSchedule = async () => {
            setLoading(true);
            setError("");

            try{
                const holidayData = await holidayListData(); //   
                const daysInMonth = new Date(year, month, 0).getDate();

                const newSchedule = Array.from({ length: daysInMonth }, (_, i) => {
                    const day = i + 1;
                    const key = `${year}-${String(month).padStart(2, '0')}-${String(day).padStart(2, '0')}`; //    (YYYY-MM-DD)
                    const weekday = new Date(year, month - 1, day).toLocaleDateString("ja-JP", { weekday: "short" });
                    const isWeekend = weekday === "" || weekday === ""; //  
                    const HolidayType = holidayData[key]  //   (  )
                    const workType = workDataList[key] || {}; //    
                    return {
                        date: `${day}`,
                        key,
                        weekday,
                        HolidayType,
                        isHoliday: workType.isHoliday ||  "",
                        checkInTime: workType.checkInTime || "",
                        checkOutTime: workType.checkOutTime || "",
                        checkOutDate: workType.checkOutDate || "",
                        memo: workType.memo || (HolidayType ? holidayData[key] : isWeekend ? "" : ""),
                        workType: workType.workType || "",
                        workLocation: workType.workLocation || "",
                        workPosition: workType.workPosition || "",
                        breakTimeIn: workType.breakTimeIn || "",
                        breakTimeOut: workType.breakTimeOut || "",
                        styleClass: HolidayType ? "holiday" : isWeekend ? "weekend" : "",
                    };
                });
                setSchedule(newSchedule);
                setLoading(false);
            }catch (error){
                setError("   ." + error);
            } finally {
                setLoading(false);
            }
        };
        fetchSchedule();
    }, [year, month, workDataList]);

    const changeMonth = (direction) => {
        setMonth((prev) => {
            let newMonth = prev + direction;
            let newYear = year;

            if (newMonth < 1) {
                newYear -= 1;
                newMonth = 12;
            } else if (newMonth > 12) {
                newYear += 1;
                newMonth = 1;
            }

            setYear(newYear);
            return newMonth;
        });
    };

    const handleClickMyPage = () => {
        const scheduleData  = {stateInfo : "true"};
        navigate("/workSchedule/dashBoard", {state: scheduleData });
    }

    const handleClickEdit = (date) => {
        navigate(`/workSchedule/detail/${date}`);
    }

    if (loading) return <p>Loading...</p>;
    if (error) return <p>{error}</p>;
        return (
            <div className="container">
                <h3 className="text-dark mb-1">WORK SCHEDULE</h3>
                <div className="d-flex justify-content-center align-items-center">
                    <button onClick={() => changeMonth(-1)}></button>
                    <h2 className="px-3">{year} / {String(month).padStart(2, "0")}</h2>
                    <button onClick={() => changeMonth(1)}></button>
                </div>
                <button type="button" className="action-button" onClick={handleClickMyPage}>
                      
                </button>
                <div className="table-responsive">
                    <table className="table table-striped">
                        <thead>
                        <tr>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                            <th className="text-center"></th>
                        </tr>
                        </thead>
                        <tbody>
                        {schedule.map((day, index) => (
                            <tr key={index} >
                                <td className={day.styleClass}>
                                    {day.date}
                                        <i className="bi bi-pencil-fill"
                                           onClick={() => handleClickEdit(day.key)}
                                        >
                                        </i>
                                </td>
                                <td className={day.styleClass}>{day.weekday}</td>
                                <td className={day.styleClass}>{day.workType} </td>
                                <td className={day.styleClass}>{day.workPosition}</td>
                                <td className={day.styleClass}>{day.checkInTime} </td>
                                <td className={day.styleClass}>{day.checkOutTime} </td>
                                <td className={day.styleClass}>{day.memo} </td>
                            </tr>
                        ))}
                        </tbody>
                    </table>
                </div>
            </div>
        );
}
export default WorkScheduleList;